import { Commands, Controller, Node } from '@ali/4ever-cangjie';
import removePureLink from '../../commands/removePureLink';
import { ILinkCardInfo, LinkPluginConfig } from '../../types';
import buildLinkCardBlock from '../utils/buildLinkCardBlock';
import { fetchLinkData } from '../utils/fetchLinkData';
import deleteExtraBlankLine from './deleteExtraBlankLine';


export default function convertPureLinkToBlockLinkCard(
  controller: Controller,
  node: Node,
  data: {
    offset: number,
    text: string,
    href: string,
    srcHref: string,
    cardInfo: ILinkCardInfo,
    needFetchLinkInfo?: boolean
    getLinkInfo?: LinkPluginConfig['getLinkInfo']
  }
) {

  const { offset, text, href, srcHref, cardInfo, needFetchLinkInfo, getLinkInfo } = data;

  // 将 pure link 转换为 block link card
  const nodeText = text.length ? text : href;

  // 如果更新了卡片信息
  const card = buildLinkCardBlock({ text: nodeText, href, cardInfo });
  controller
    .setOpRelatedId(card.key)
    .command(removePureLink, node, offset, srcHref)
    .command('insertCard', card)
    .command(deleteExtraBlankLine, card.key);

  if (needFetchLinkInfo) {
    fetchLinkData(card, controller, href, nodeText, getLinkInfo, card.key);
  }
  return controller;
};

